import React from 'react';
import './Square.css'

function Square(props) {
  const myStyle = () => {
    return ({
      fontWeight: props.isBold ? 'bold' : 'normal',
      backgroundColor: props.isWin ? "DodgerBlue" : ""
    }); 
  }
  return (
    <button className="square" onClick={props.onClick} style={myStyle()}>
      {props.value}
    </button>
  );

}

export default Square;
