import React, { useState } from 'react';
import Board from './../Board/Board'
import './Game.css';
import { calculateWinner } from './Service'

function Game() {
  const [rows, setRows] = useState(3);
  const [cols, setCols] = useState(3);
  const [condition, setCondition] = useState(3);
  const [stepNumber, setStepNumber] = useState(0);
  const [des, setDes] = useState(false);
  const [xIsNext, setXIsnext] = useState(true);
  const [history, setHistory] = useState([
    {
      squares: Array(rows*cols).fill(null),
      position: null
    }
  ]);
  const [inforGame, setInforGame] = useState({
    rows: 3,
    cols: 3,
    condition: 3
  })


  const handleClick = (i) => {
    const newHistory = history.slice(0, stepNumber + 1);
    const current = newHistory[newHistory.length - 1];
    const squares = current.squares.slice();
    if (calculateWinner(squares, current.position, rows, cols, condition).value || squares[i]) {
      return;
    }
    squares[i] = xIsNext ? "X" : "O";
    setHistory(newHistory.concat([
      {
        squares: squares,
        position: i
      }
    ]))

    setStepNumber(newHistory.length);
    setXIsnext(!xIsNext);
  }

 
  const jumpTo = (step) => {
    setStepNumber(step);
    setXIsnext((step % 2) === 0)
  }

  const handleSubmit = (event) =>{
    event.preventDefault();
    if(inforGame.condition > inforGame.rows || inforGame.condition > inforGame.cols){
      alert("Äiá»u kiá»n tháº¯ng khÃ´ng há»£p lá»")
    }

    setRows(inforGame.rows);
    setCols(inforGame.cols);
    setCondition(inforGame.condition);

    setStepNumber(0);
    setDes(false);
    setXIsnext(true);
    setHistory([
      {
        squares: Array(inforGame.rows*inforGame.cols).fill(null),
        position: null
      }
    ]);
  }

  const handleChange = (event) => {
    const target = event.target;
    const name = target.name;
    setInforGame({
      ...inforGame,
      [name]: Number(target.value)
    })
  }
  const currentHistory = history;
  const current = currentHistory[stepNumber];
  const winner = calculateWinner(current.squares, current.position, rows, cols, condition);

  const set = current.squares.filter(x => x === null).length;

  let moves = currentHistory.map((step, move) => {
    const desc = move ?
      'Go to move #' + move + " ( " + Math.floor(step.position / cols) + ", " + step.position % cols + " )" :
      'Go to game start';
    return (
      <li key={move}>
        {stepNumber === move ? <button onClick={() => jumpTo(move)} style={{ fontWeight: 'bold' }}>{desc}</button> : <button onClick={() => jumpTo(move)}>{desc}</button>}
      </li>
    );
  });

  let status = winner.value ? 'Winner: ' + winner.value : (set === 0 ? "Equal" : 'Next player: ' + (xIsNext ? 'X' : 'O'));

  return (
    <div className="game">

      <div className="game-board">
        <Board squares={current.squares} onClick={handleClick} position={current.position} isWin={winner} rows = {rows} cols = {cols}/>
      </div>
      <div className="game-info">
        <div>{status}</div>
        <button onClick={() => setDes(!des)}>Sort</button>
        <ol>{des ? moves.reverse() : moves}</ol>
      </div>
      <div className="game-info">
        <form onSubmit = {handleSubmit}>
          <div className="form-group">
            <label >Rows</label>
            <input type="number" className="form-control" value = {inforGame.rows} name = "rows" onChange = {handleChange}/>
          </div>
          <div className="form-group">
            <label >Cols</label>
            <input type="number" className="form-control" value = {inforGame.cols} name = "cols"  onChange = {handleChange}/>
          </div>
          <div className="form-group">
            <label >Condition</label>
            <input type="number" className="form-control" value = {inforGame.condition} name = "condition" id="condition" onChange = {handleChange}/>
          </div>
          <button  className="btn btn-primary">Apply</button>
        </form>
      </div>
    </div>
  );

}

export default Game;

