import React, { Fragment } from 'react';
import Square from './../Square/Square'

function Board(props) {

  const renderSquare = (i) => {
    return (
      <Square
        value={props.squares[i]}
        onClick={() => props.onClick(i)}
        isBold={props.position === i}
        isWin={props.isWin.position.includes(i)}
        key={i}
      />
    );
  }

  let rows = [];
  let cols = [];

  for (let i = 0; i < props.rows; i++)
    rows = [...rows, i];
  for (let i = 0; i < props.cols; i++)
    cols = [...cols, i];

  const squares = rows.map((value, index) => {
    const elements = cols.map(item => renderSquare(value * cols.length + item))
    return (
      <div className="board-row" key={index}>
        {elements}
      </div>
    );
  })

  return (
    <Fragment>
      {squares}
    </Fragment>
  );

}
export default Board;
